.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
   
  .box {
    position: relative;
    width: auto;
    min-width: 250px;
    margin: 0 0 0 0vw;
    height: auto;
    max-height: 80vh;
    background: #fff;
    border-radius: 4px;
    padding: 20px;
    border: 1px solid #999;
    overflow: auto;
  }

  

  .box input{
    width: 100%;
    font-size: calc(10px + .5vw);
  }
   
  .close-icon {
    content: 'x';
    cursor: pointer;
    position: absolute;
    right: calc(10px);
    top: calc(10px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
  }

  .box .row{
    width: 100%;
  }

  .box .edit-office-container{
    box-shadow: none;
    margin-left: -3%;
  }